import firebase from "firebase";
import { db } from "@/firebase/firebaseInit";
import router from "@/router";
import * as types from "./types";
import { translateError } from "@/helpers/translateError";

export default {
  // Регістрація користувача
  userSignUp({ commit }, payload) {
    commit(types.SET_LOADING, true);
    firebase
      .auth()
      .createUserWithEmailAndPassword(payload.email, payload.password)
      .then(user => {
        commit(types.SET_USER, { email: user.email, uid: user.uid });
        commit(types.SET_LOADING, false);
        router.push("/");
      })
      .catch(error => {
        commit(types.SET_ERROR, true);
        commit(types.SHOW_ALERT, {
          text: translateError(error.code, error.message)
        });
        commit(types.SET_LOADING, false);
      });
  },

  // Авторизація/вхід користувача
  userSignIn({ commit }, payload) {
    commit(types.SET_LOADING, true);
    firebase
      .auth()
      .signInWithEmailAndPassword(payload.email, payload.password)
      .then(user => {
        commit(types.SET_USER, { email: user.email, uid: user.uid });
        commit(types.SET_LOADING, false);
        router.push("/");
      })
      .catch(error => {
        commit(types.SET_ERROR, true);
        commit(types.SHOW_ALERT, {
          text: translateError(error.code, error.message)
        });
        commit(types.SET_LOADING, false);
      });
  },

  // Автоматична авторизація користувача
  autoSignIn({ commit }, payload) {
    commit(types.SET_USER, { email: payload.email, uid: payload.uid });
  },

  // Вихід користувача
  userSignOut({ commit }) {
    firebase
      .auth()
      .signOut()
      .then(() => {
        commit(types.SET_USER, null);
        router.push("/signin");
      });
  },

  // Закривання панелі з повідомленням
  closeAlert({ commit }) {
    commit(types.CLOSE_ALERT);
  },

  // Загрузка всіх задач з БД
  fetchTasks({ commit, state }) {
    commit(types.SET_GLOBAL_LOADING, true);
    db
      .ref(`/tasks/${state.user.uid}`)
      .once("value")
      .then(data => {
        const tasks = [];
        const obj = data.val();
        for (let key in obj) {
          tasks.push({
            id: key,
            title: obj[key].title,
            description: obj[key].description,
            createTime: obj[key].createTime,
            deadline: obj[key].deadline,
            isDone: obj[key].isDone,
            isSelected: false
          });
        }
        const tasksSorted = tasks.sort((a, b) => {
          return new Date(b.createTime) - new Date(a.createTime);
        });
        commit(types.SET_TASKS, tasksSorted);
        commit(types.SET_GLOBAL_LOADING, false);
      })
      .catch(error => {
        commit(types.SET_ERROR, true);
        commit(types.SHOW_ALERT, {
          text: translateError(error.code, error.message)
        });
        commit(types.SET_GLOBAL_LOADING, true);
      });
  },
  // Запис нової задачі в БД
  createNewTask({ commit, state }, payload) {
    let newTask = {};
    if (payload.description && payload.deadline) {
      newTask = {
        title: payload.title,
        description: payload.description,
        createTime: new Date().toISOString(),
        deadline: payload.deadline.toISOString(),
        isDone: false
      };
    } else if (payload.description) {
      newTask = {
        title: payload.title,
        description: payload.description || null,
        createTime: new Date().toISOString(),
        isDone: false
      };
    } else if (payload.deadline) {
      newTask = {
        title: payload.title,
        createTime: new Date().toISOString(),
        deadline: payload.deadline.toISOString(),
        isDone: false
      };
    } else {
      newTask = {
        title: payload.title,
        createTime: new Date().toISOString(),
        isDone: false
      };
    }
    commit(types.SET_LOADING, true);
    db
      .ref(`/tasks/${state.user.uid}`)
      .push(newTask)
      .then(data => {
        const key = data.key;
        commit(types.CREATE_NEW_TASK, {
          ...newTask,
          id: key,
          isSelected: false
        });
        commit(types.SET_LOADING, false);
      })
      .catch(error => {
        commit(types.SET_ERROR, true);
        commit(types.SHOW_ALERT, {
          text: translateError(error.code, error.message)
        });
        commit(types.SET_LOADING, false);
      });
  },
  // Видалення задачі з БД
  deleteTask({ commit, state }, payload) {
    db
      .ref(`/tasks/${state.user.uid}/${payload.id}`)
      .remove()
      .then(() => {
        commit(types.DELETE_TASK, payload);
      })
      .catch(error => {
        commit(types.SET_ERROR, true);
        commit(types.SHOW_ALERT, {
          text: translateError(error.code, error.message)
        });
      });
  },
  // Зміна статусу задачі на зроблено/не зроблено
  checkTask({ commit, state }, payload) {
    const checkTask = {};
    checkTask.isDone = !payload.isDone;
    commit(types.SET_LOADING, true);
    db
      .ref(`/tasks/${state.user.uid}/${payload.id}`)
      .update(checkTask)
      .then(() => {
        commit(types.CHECK_TASK, payload);
        commit(types.SET_LOADING, false);
      })
      .catch(error => {
        commit(types.SET_ERROR, true);
        commit(types.SHOW_ALERT, {
          text: translateError(error.code, error.message)
        });
        commit(types.SET_LOADING, false);
      });
  },
  // Аккордион для задач
  selectedTask({ commit }, payload) {
    commit(types.SELECTED_TASK, payload);
  }
};



