import * as types from "./types";

export default {
  // Записує дані авторизованого користувача
  [types.SET_USER](state, payload) {
    state.user = payload;
  },
  // Записує чи є помилка
  [types.SET_ERROR](state, payload) {
    state.hasError = payload;
  },

  // Індикатор загрузки
  [types.SET_LOADING](state, payload) {
    state.isLoading = payload;
  },

  // Індикатор глобальної загрузки
  [types.SET_GLOBAL_LOADING](state, payload) {
    state.isGlobalLoading = payload;
  },

  // Виводить панель з повідомленням
  [types.SHOW_ALERT](state, payload) {
    state.alert.text = payload.text;
    state.alert.multiline = payload.text.length > 50 ? true : false;

    if (payload.multiline) {
      state.alert.multiline = payload.multiline;
    }

    if (payload.timeout) {
      state.alert.timeout = payload.timeout;
    }

    state.alert.visible = true;
  },

  // Скидає до станд. значень панель з повідомленням
  // Стирає помилку
  [types.CLOSE_ALERT](state) {
    state.alert.visible = false;
    state.alert.multiline = false;
    state.alert.timeout = 5000;
    state.alert.text = null;
    state.hasError = false;
  },

  // Запис задач з БД в сховище (vuex/state)
  [types.SET_TASKS](state, payload) {
    state.tasks = payload;
  },

  // Додавання нової задачі в сховище (vuex/state)
  [types.CREATE_NEW_TASK](state, payload) {
    state.tasks.unshift(payload);
  },

  // Видалення задачі з сховища (vuex/state)
  [types.DELETE_TASK](state, payload) {
    const index = state.tasks.findIndex(task => task.id == payload.id);
    state.tasks.splice(index, 1);
  },

  [types.CHECK_TASK](state, payload) {
    let task = state.tasks.find(task => {
      return task.id == payload.id;
    });
    task.isDone = !payload.isDone;
  },

  // Аккордион для задач
  [types.SELECTED_TASK](state, payload) {
    let task = state.tasks.find(task => {
      return task.id == payload.id;
    });
    for (let i = 0; i < state.tasks.length; i++) {
      if (state.tasks[i].id === task.id) {
        if (task.isSelected == true) {
          task.isSelected = false;
        } else {
          task.isSelected = true;
        }
      } else {
        state.tasks[i].isSelected = false;
      }
    }
  }
};



