export default {
  user(state) {
    return state.user;
  },
  isLoading(state) {
    return state.isLoading;
  },
  isGlobalLoading(state) {
    return state.isGlobalLoading;
  },
  hasError(state) {
    return state.hasError;
  },
  isAuthenticated(state) {
    return state.user !== null && state.user !== undefined;
  },
  alert(state) {
    return state.alert;
  },
  getTasks(state) {
    return state.tasks;
  }
};



