<template>
  <v-app>
    <c-toolbar></c-toolbar>
    <v-content>
      <c-alert></c-alert>
      <router-view/>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: "App",
  data() {
    return {};
  },
  components: {
    "c-toolbar": () => import(`@/components/Toolbar.vue`),
    "c-alert": () => import(`@/components/Alert.vue`)
  }
};
</script>



