export default value => {
  const date = new Date(value);
  const options = {
    year: "numeric",
    month: "short",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
    weekday: "short"
  };
  return date.toLocaleString("uk-UA", options);
};



