export function translateError(code, message) {
  switch (code) {
    case "auth/wrong-password":
      return "Неправильний пароль.";
    case "auth/weak-password":
      return "Пароль занадто слабкий.";
    case "auth/user-not-found":
      return "Електронна адреса не існує або вже зайнята.";
    case "auth/too-many-requests":
      return "Доступ до облікового запису заблокований через незвичну активність. Спробуйте ще раз пізніше.";
    case "auth/email-already-in-use":
      return "Електронна адреса вже зайнята.";
    default:
      return message;
  }
}



