import Vue from "vue";
import firebase from "firebase";
import Router from "vue-router";

const routerOptions = [
  { path: "/", component: "Home", meta: { requiresAuth: true } },
  { path: "/signin", component: "SignIn", meta: { isAnonymous: true } },
  { path: "/signup", component: "SignUp", meta: { isAnonymous: true } },
  { path: "/*", redirect: "/" }
];

const routes = routerOptions.map(route => {
  return {
    ...route,
    component: () => import(`@/views/${route.component}.vue`)
  };
});

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes
});

// Провірка до яких сторінок є доступ
// Чи зареєстрований користувач ? головна сторінка : сторінка входу
router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
  const isAnonymous = to.matched.some(record => record.meta.isAnonymous);
  const isAuthenticated = firebase.auth().currentUser;
  if (requiresAuth && !isAuthenticated) {
    next("/signin");
  } else if (isAnonymous && isAuthenticated) {
    next("/");
  } else {
    next();
  }
});

export default router;



